import axios from 'axios';
export default class App {
    render() {
        const page = document.querySelector('#page');
        page.innerHTML = this.renderLoader();
        axios.get('/posts')
        .then((response) => {
        this.renderPosts(response.data);
        });
    }

    // render all posts
    renderPosts(posts) {
    const page = document.querySelector('#page');
      let postsMarkup = '';
  
      for (let i = 0; i < posts.length; i++) {
        postsMarkup += this.renderPost(posts[i]);
        }
        page.innerHTML = postsMarkup;
  
  }
  
  // render single post
  renderPost(post) {
    const title = post.title;
    const content = this.truncateContent(post.content);
  
    return `<h2>${title}</h2><p>${content}</p>${this.renderLink()}<hr />`;
  }
  
  // truncates post to 500 characters
  truncateContent(content) {
    return `${content.slice(0, 500)}...`;
  }
  
  // "Read More" Button
  renderLink() { 
    return `<a href="#">Read More</a>`;
  }
  
  renderLoader() {
    return `<img class="loader" src="/images/loader.gif" alt="Loading...">`;
  }
}

